const togglePasswordTriggerList = document.querySelectorAll('[data-bs-toggle="password"]');
const togglePasswordList = [...togglePasswordTriggerList].map(togglePasswordTriggerEl => {

    let input = togglePasswordTriggerEl.querySelector("input");
    let span = togglePasswordTriggerEl.querySelector("span");
    span.classList.add("toggle-password-hide");

    span.addEventListener("click", function() {
        if(input.type === 'password'){
            input.type = 'text';
            span.classList.remove("toggle-password-hide");
            span.classList.add("toggle-password-show");
        }
        else{
            input.type = 'password';
            span.classList.remove("toggle-password-show");
            span.classList.add("toggle-password-hide");
        }
    });
});