const countCharactersTriggerList = document.querySelectorAll('[data-bs-toggle="count-characters"]');
const countCharactersList = [...countCharactersTriggerList].map(countCharactersTriggerEl => {

    let input = countCharactersTriggerEl.querySelector(".count-characters-input");
    let output = countCharactersTriggerEl.querySelector(".count-characters-output");

    if(input.maxLength > '0'){
        let inputMaxLength = input.maxLength.toString().length;
        let outputValue = '0'.repeat(inputMaxLength)+' / '+input.maxLength;
        
        output.innerText = outputValue;
    }
    else{
        output.innerText = '0';
    }

    input.addEventListener("keyup", function() {
        if(input.maxLength > '0'){
            let inputMaxLength = input.maxLength.toString().length;
            let outputMaxLength = input.value.length.toString().length;
            let NewLength = inputMaxLength - outputMaxLength;
            let outputValue = '0'.repeat(NewLength)+input.value.length+' / '+input.maxLength;

            output.innerText = outputValue;
        }
        else{
            output.innerText = input.value.length;
        } 
    });
});