
const countdownTriggerList = document.querySelectorAll('[data-bs-toggle="countdown"]');
const countdownList = [...countdownTriggerList].map(countdownTriggerEl => {
    var countdown = setInterval(function (){
        let timeleft = new Date(countdownTriggerEl.dataset.bsCountdown).getTime() - new Date().getTime()
        let days = Math.floor(timeleft / (1000 * 60 * 60 * 24));
        let hours = Math.floor((timeleft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        let minutes = Math.floor((timeleft % (1000 * 60 * 60)) / (1000 * 60));
        let seconds = Math.floor((timeleft % (1000 * 60)) / 1000);

        countdownDays = countdownTriggerEl.querySelector('.countdown-days');
        countdownHours = countdownTriggerEl.querySelector('.countdown-hours');
        countdownMinutes = countdownTriggerEl.querySelector('.countdown-minutes');
        countdownSeconds = countdownTriggerEl.querySelector('.countdown-seconds');

        countdownDays.innerText = days;
        countdownHours.innerText = hours;
        countdownMinutes.innerText = minutes;
        countdownSeconds.innerText = seconds;

        console.log();
        if (timeleft < 0) {
            clearInterval(countdown);
            countdownDays.innerText = '00';
            countdownHours.innerText = '00';
            countdownMinutes.innerText = '00';
            countdownSeconds.innerText = '00';
        }
    }, 1000)
});