const sidebarTriggerList = document.querySelectorAll('[data-bs-toggle="sidebar"]');
const sidebarList = [...sidebarTriggerList].map(sidebarTriggerEl => {

    sidebarTriggerEl.addEventListener("click", function() {
        let element = document.getElementById(sidebarTriggerEl.dataset.bsTarget)
        let style = window.getComputedStyle(element);
        let propertyValue = style.getPropertyValue('display');
        
        if(propertyValue === 'block') element.style.setProperty('display', 'none');
        else element.style.setProperty('display', 'block');
    });
});