const gttTriggerList = document.querySelectorAll('[data-bs-toggle="go-to-top"]');
const gttList = [...gttTriggerList].map(gttTriggerEl => {
    gttTriggerEl.addEventListener("click", function (){
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    });
    window.onscroll = function() {
        if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
            gttTriggerEl.style.display = "flex";
        } else {
            gttTriggerEl.style.display = "none";
        }
    };
});