const toastTriggerList = document.querySelectorAll('[data-bs-toggle="toast"]');
const toastList = [...toastTriggerList].map(toastTriggerEl => {
    toastTriggerEl.addEventListener("click", function() {
        let toastLive = document.getElementById(toastTriggerEl.dataset.bsTarget)
        const toast = new bootstrap.Toast(toastLive)
        toast.show();
    });
});