var theme = 'light';

if(localStorage.getItem('theme')){
    theme = localStorage.getItem('theme');
}
else{
    localStorage.setItem('theme', 'light');
}

var html = document.querySelector('html');
html.dataset.bsTheme = theme;

const DarkModeTriggerList = document.querySelectorAll('[data-bs-toggle="dark-mode"]');
const DarkModeList = [...DarkModeTriggerList].map(DarkModeTriggerEl => {

    DarkModeTriggerEl.addEventListener("click", function() {
        if(html.dataset.bsTheme === 'light'){
            html.dataset.bsTheme = 'dark';
            localStorage.setItem('theme', 'dark');
        }
        else{
            html.dataset.bsTheme = 'light';
            localStorage.setItem('theme', 'light');
        }
    });
});